@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:ital,wght@0,400;0,600;0,700;1,400;1,600;1,700&family=Work+Sans:ital,wght@0,400;0,500;0,600;1,400;1,600&display=swap);
:root{
    --accent-color: rgb(13,202,240);
    --display-text: none;
    --text-align: center;
}
@media (min-width: 800px) {
    :root{
        --display-text: block;
        --text-align: left;
    }
}
  

h1,h2,h3,h4,h5,h6,body,p{
    margin: 0;
}
h1,h2,h4,h5,h6{
    font-family: 'Crimson Text', serif;
    font-weight: 400;
}
body, h3{
    font-family: 'Work Sans', sans-serif;
    font-weight: 500;
}
h3{
    color: rgb(13,202,240);
    color: var(--accent-color);
    font-weight: 700;
}

h1{
    font-size: clamp(2rem, 1rem + 5vw, 7rem);
}
p{
    font-size: clamp(1rem, 1rem + 2vw, 1.5rem);
}
a{
    color: rgb(13,202,240) !important;
    color: var(--accent-color) !important;
}
/* for general usage by all elements */
.centered{
    display: flex;
    justify-content: center;
}
.flex-vertical{
    display: flex;
    flex-direction: column;
}
.flex-horizontal{
    display: flex;
}
.flex-evenly{
    justify-content: space-evenly;
}
.NFT-contain{
    display: flex;
    flex-direction: column;
    margin-left: 10%;
    margin-right: 10%;
}
/* using important to override styling from Bootstrap css */
.btn{
    color: white !important;
    font-family: 'Work Sans', sans-serif !important;
    font-weight: 500 !important;
}
.button-contain{
    display: flex;
    justify-content: center;
}
.NFT-image{
    object-fit: contain;
    height: clamp(13rem, 10rem + 25vw, 25rem);
    width: auto;
}
@media (min-width: 800px) and (max-width: 1200px) {
    .NFT-contain{
        flex-direction: row;
        grid-gap: 5%;
        gap: 5%;
        margin-left: 10%;
        margin-right: 10%;
    }
    .button-contain{
        display: block;
    }
}
@media (min-width: 1200px) {
    .NFT-contain{
        flex-direction: row;
        grid-gap: 5%;
        gap: 5%;
        margin-left: 20%;
        margin-right: 20%;
    }
    .button-contain{
        display: block;
    }
}

.NFT-title{
    text-align: var(--text-align);
}
.content-text{
    display: var(--display-text);
    text-align: var(--text-align);
}


.ticket-contain{
    height: clamp(5rem, 3rem + 12vw, 12.5rem);
    width: clamp(8rem, 4.8rem + 19.2vw, 20rem);

    background-image: url(/nft-preview/static/media/Ticket.9a7edf52.svg);
    background-size: contain;
    background-repeat: no-repeat;
    -webkit-filter: drop-shadow(0rem 0.2rem 0.2rem rgba(0, 0, 0, 0.2));
            filter: drop-shadow(0rem 0.2rem 0.2rem rgba(0, 0, 0, 0.2));
}

.golden-ticket-contain{
    height: clamp(5rem, 3rem + 12vw, 12.5rem);
    width: clamp(8rem, 4.8rem + 19.2vw, 20rem);

    background-image: url(/nft-preview/static/media/GoldenTicket.8bbf61b3.svg);
    background-size: contain;
    background-repeat: no-repeat;
    -webkit-filter: drop-shadow(0rem 0.2rem 0.2rem rgba(0, 0, 0, 0.2));
            filter: drop-shadow(0rem 0.2rem 0.2rem rgba(0, 0, 0, 0.2));

    /* styling the text */
    color:rgba(255, 255, 255, 0.746);
}
.golden-header{
    font-size: clamp(.8rem, .5rem + 2vw, 2rem);
}
.ticket-heading{
    font-size: clamp(.8rem,.5rem + 2vw, 2rem);
}
.ticket-content{
    /* ensure no overflow onto the cutout portions of the ticket image */
    padding-right: 1rem;
    padding-left: 1rem;

    display: flex;
    justify-content: space-around;
}
.ticket-text{
    display: none;
    font-size: .8rem;
    margin: 0;
}
.thumbnail{
    width: 30%;
}
/* directly overriding default styles of the bootstrap button (careful!) */
.button-direct{
    font-size: .60rem !important;
}
@media (min-width: 600px) {
    .button-direct{
        font-size: .875rem !important;
    }
}

@media (min-width: 1050px) {
    .ticket-text{
        display: block;
    }
    .ticket-button{
        display: none;
    }
    .ticket-content{
        display: grid;
        grid-template-columns: 28% 72%;
        grid-template-rows: 80%;
        grid-gap: .25rem;

        padding-right: 1.5rem;
        padding-left: 1.5rem;
    }
    .thumbnail{
        width: 100%;
    }
}
.tickets-grid{
    display: grid;
    grid-template-columns: clamp(8rem, 4.8rem + 19.2vw, 20rem) clamp(8rem, 4.8rem + 19.2vw, 20rem);
    grid-template-rows: clamp(5rem, 3rem + 12vw, 12.5rem) clamp(5rem, 3rem + 12vw, 12.5rem);
    grid-gap: 10px;
}
.tickets-contain{
    display:flex;
    justify-content: center;
}
@media (min-width: 1200px) {
    .tickets-grid{
        display: flex;
        justify-content: space-between;
    }
}
.header-wrap{
    margin-top: 2rem;
    margin-bottom: 1rem;
}
.header-underline{
    width: 80%;
    border-bottom: 2px solid var(--accent-color);
}
